var pageModule;
var pageModuleIsLoadComplete = false;
var waitReceiveMessage=[];
var waitChildrenReceiveMessage=[];
/**
 * module
 * 
 * {
 * pageid:"",
 * routerId:"",
 * pageModule:{},
 * pageModuleIsLoadComplete:false,
 * waitReceiveMessage:[],
 * waitChildrenReceiveMessage:[]
 * }
 */
var pageModuleList=[];
/**
 * 
 * @param {*} object 
 */
function registerModule(object,pageid,routerId){
    if(!routerId){
        //
        this.pageModule = object;
    }
    if(!pageid&&!routerId){
        this.pageModuleList=[{
            pageid:object.id,
            routerId,
            pageModule:object,
            pageModuleIsLoadComplete:false,
            waitReceiveMessage:[],
            waitChildrenReceiveMessage:[]
        }];
    }
    var hasExists = this.pageModuleList.filter(item=>item.routerId==routerId)
    //
    if(hasExists.length==0){
        this.pageModuleList.push({
            pageid,
            routerId,
            pageModule:object,
            pageModuleIsLoadComplete:false,
            waitReceiveMessage:[],
            waitChildrenReceiveMessage:[]
        })
    }
}
/**
 * 
 * @param {*} routerId 
 */
function unRegisterModule(routerId){
    for (let index = 0; index < this.pageModuleList.length; index++) {
        const element = this.pageModuleList[index];
        if(element.routerId==routerId){
            this.pageModuleList.splice(index,1);
        }
    }
}
/**
 * 
 */
function pageLoadCompleteAfter(routerId){
    var module_list = this.pageModuleList.filter(item=>item.routerId==routerId);
    if(module_list.length>0){
        var moduleObject = module_list[0];
        moduleObject.pageModuleIsLoadComplete = true;
        //100,100
        //
        let that = this;
        setTimeout(() => {
            moduleObject.waitReceiveMessage.forEach(item=>{
                that.callModuleReceiveHandle(item,routerId)
            });
            moduleObject.waitReceiveMessage = [];
            moduleObject.waitChildrenReceiveMessage.forEach(item=>{
                that.sendChildren(item,routerId)
            });
            moduleObject.waitChildrenReceiveMessage = [];
        }, 100);
    }
}
/**
 * 
 */
function pageModuleLoadCompleteBefore(routerId){
    var module_list = this.pageModuleList.filter(item=>item.routerId==routerId);
    if(module_list.length>0){
        var moduleObject = module_list[0];
        moduleObject.pageModuleIsLoadComplete = false;
    }
}

/**
 * 
 * @param {
 *  type:"",
 *  message:{},
 *  module:{packageid:"",containerIndex:""} or [{packageid:"",containerIndex:""}]   packageid  containerIndex
 * } object 
 */
function sendChildren(object,routerId){
    if(!object||(object&&!object.module)){
        return;
    }
    this.pageModuleList&&this.pageModuleList.forEach(pageObject=>{
        if(!routerId||pageObject.routerId==routerId){
            if(!pageObject.pageModuleIsLoadComplete){
                //
                pageObject.waitChildrenReceiveMessage.push(object);
                return;
            }
            var module = object.module;
            if(module instanceof Object){
                //
                module = [module]
            }
            for (let index = 0; index < module.length; index++) {
                const element = module[index];
                this.sendChildrenInner(null,element,object,pageObject)
            }
        }
    });
}
/**
 * 
 * @param {*} moduleObject {packageid:"",containerIndex:""}
 * @param {*} object 
 */
function sendChildrenInner(children,moduleObject,object,pageObject){
    if(pageObject.pageModule){
        var layout = children|| pageObject.pageModule.page.layout.children;
        layout&&layout.forEach(item=>{
            if(item.packageid==moduleObject.packageid){
                //,containerIndex-1containerIndex
                item.children&&item.children.forEach(sitem=>{
                    if(sitem.containerIndex==moduleObject.containerIndex||!moduleObject.containerIndex||moduleObject.containerIndex=="-1"||moduleObject.containerIndex==0){
                        //
                        pageObject.pageModule.page.componentsMap.forEach(ssitem=>{
                            if(sitem.packageid==ssitem.packageid){
                                //
                                ssitem.idmBroadcastMessage&&ssitem.idmBroadcastMessage(object)
                            }
                        })
                    }
                })
            }else if(item.children&&item.children.length>0){
                this.sendChildrenInner(item.children,moduleObject,object,pageObject);
            }
        })
    }
}

/**
 * 
 * @param {
 *  type:"",
 *  message:{},
 *  rangeModule:"keypackageid",
 *  className:""
 *  globalSend:truerangeModuleclassNamefalse
 * } object 
 * @param {String} routerId IDID
 */
function send(object,routerId){
    this.callModuleReceiveHandle({
        //
        type:"direct",
        object
    },routerId) 
}
/**
 * 
 * @param {*} object 
 * @param {String} routerId IDID
 */
function bridgeSend(object,routerId){
    //isAcrosstrue
    this.callModuleReceiveHandle({
        //
        type:"across",
        object
    },routerId)
}
/**
 * 
 * @param {
 *  type:"direct  across",
 *  object
 * } object 
 * @param {String} routerId IDID
 */
function callModuleReceiveHandle(object,routerId){
    if(!routerId){
        routerId="";
    }
    //routerId routerId
    this.pageModuleList&&this.pageModuleList.forEach(moduleObject=>{
        if(routerId===""||moduleObject.routerId==routerId){
            if(!moduleObject.pageModuleIsLoadComplete){
                //
                moduleObject.waitReceiveMessage.push(object);
                return;
            }
            //schemajson
            if(moduleObject.pageModule){
                var isAcross = object.type!="direct";
                moduleObject.pageModule.page.componentsMap.forEach(item=>{
                    if(item.className==object.object.className||!object.object.className){
                        var rangeModule = object.object.rangeModule;
                        //
                        if(!rangeModule||(rangeModule&&rangeModule.length==0)||((rangeModule instanceof Array)&&_.includes(rangeModule,item.packageid))){
                            //
                            item.idmBroadcastMessage&&item.idmBroadcastMessage({
                                ...object.object,
                                isAcross:isAcross
                            })
                            /**
                             * 
                             */
                            if(object.object.type=="linkageOpenDialog"&&object.object.message.layerOption){
                                //openUrl
                                const layerOption = object.object.message.layerOption,moduleId=item.packageid;
                                if(layerOption.openUrl){
                                    if(typeof layerOption.openUrl === "string"){
                                        //
                                        setTimeout(() => {
                                            document.querySelector("#"+moduleId+" iframe").src = layerOption.openUrl;
                                        }, 600);
                                    }else if(layerOption.openUrl instanceof Array){
                                        //
                                        setTimeout(() => {
                                            layerOption.openUrl.forEach(layerObject=>{
                                                document.querySelector("#"+moduleId+" iframe[name='"+layerObject.name+"']").src = layerObject.url;
                                            })
                                        }, 600);
                                    }
                                }
                                layerOption.success&&layerOption.success(item);
                            }
                        }
                    }
                })
            }
        }
    })
    if(!object.object.globalSend){
        return true;
    }
    //object.type=="direct" 
    if(object.type=="direct"){
        //
        this.findParentModuleHandle(_.cloneDeep(object),routerId)
    }
    //iframe()
    if(object.type!="parent"){
        if(window.frames.length>0){
            for (let index = 0; index < window.frames.length; index++) {
                try {
                    const iframe = window.frames[index];
                    if(iframe.IDM){
                        iframe.IDM.broadcast&&iframe.IDM.broadcast.bridgeSend(_.cloneDeep(object.object),routerId);
                    }
                } catch (error) {
                    console.log("",error)
                }
            }
        }
    }else{
        //
        this.findParentModuleHandle(_.cloneDeep(object),routerId)
    }
}
/**
 * callModuleReceiveHandle
 * @param {*} object 
 */
function findParentModuleHandle(object,routerId){
    object.type = "parent";
    //
    if(parent.location.href!=location.href){
        //
        parent.IDM&&parent.IDM.broadcast.callModuleReceiveHandle(object,routerId)
    }
}
/**
 * 
 * @param [
 *  {
 *  moduleId:packageid,
 *  moduleName:asName
 *  }
 * ] pageModuleList 
 * @param {*} groupKey 
 * @param {String} routerId ID
 * 
 * return {resultData,errorData}
 */
function getModuleContextValue(pageModuleList,groupKey,routerId){
    if(!(pageModuleList instanceof Array)&&pageModuleList)
    {
        pageModuleList = [pageModuleList];
    }
    var rangeModule = [];
    pageModuleList&&pageModuleList.forEach(item=>{
        rangeModule.push(IDM.type(item)=="string"?item:item.moduleId);
    })
    var resultData = [],errorData=[];
    //routerId routerId
    this.pageModuleList&&this.pageModuleList.forEach(pageObject=>{
        if(!routerId||pageObject.routerId==routerId){
            if(pageObject.pageModule){
                pageObject.pageModule.page.componentsMap.forEach(item=>{
                    //keyformGroupKey
                    var formGroupKey = item.props&&item.props.compositeAttr?item.props.compositeAttr.formGroupKey:"";
                    if(_.includes(rangeModule,item.packageid)||(formGroupKey==groupKey&&formGroupKey!='')){
                        if(item.idmGetContextValue){
                            let rtnObject = item.idmGetContextValue();
                            if(!rtnObject){
                                return;
                            }
                            if(rtnObject.type=="success"){
                                //
                                resultData.push(rtnObject)
                            }else if(rtnObject.type=="error"){
                                //
                                errorData.push(rtnObject)
                            }
                        }
                    }
                })
            }
        }
    });
    return {resultData,errorData};
}
/**
 * 
 * @param [
 *  {
 *  moduleId:packageid,
 *  moduleName:asName
 *  }
 * ] pageModuleList 
 * @param {*} groupKey 
 * 
 * @param {String} routerId ID
 * 
 * return ["packageid",...]
 */
function getRangeModule(pageModuleList,groupKey,routerId){
    if(!(pageModuleList instanceof Array)&&pageModuleList)
    {
        pageModuleList = [pageModuleList];
    }
    var rangeModule = [];
    pageModuleList&&pageModuleList.forEach(item=>{
        if(item&&item.moduleId){
            rangeModule.push(item.moduleId);
        }
    })
    this.pageModuleList&&this.pageModuleList.forEach(pageObject=>{
        if(!routerId||pageObject.routerId==routerId){
            if(pageObject.pageModule){
                pageObject.pageModule.page.componentsMap.forEach(item=>{
                    //keyformGroupKey
                    var formGroupKey = item.props&&item.props.compositeAttr?item.props.compositeAttr.formGroupKey:"";
                    if(!_.includes(rangeModule,item.packageid)&&formGroupKey==groupKey&&formGroupKey!=''){
                        rangeModule.push(item.packageid)
                    }
                })
            }
        }
    });
    return rangeModule;
}
/**
 * 
 * @param {
 * moduleId:"ID",
 * openUrl:"urliframeiframe[{name:'iframename',url:""}]",
 * success:,
 * yes:,
 * cancel:,
 * end:
 * } option 
 */
function openDialog(option){
    this.callLayerHandle({
        //
        type:"direct",
        oType:"open",
        option
    })
}
/**
 * 
 * @param {
 * moduleId:"ID" or ["ID1","ID2"]
 * } option 
 */
function closeDialog(option){
    this.callLayerHandle({
        //
        type:"direct",
        oType:"close",
        option
    })
}


/**
 * 
 * @param {
 *  type:"direct  across",
 *  oType:"open  close"
 *  option
 * } object 
 */
function callLayerHandle(object){
    if(object.oType=="open"){
        window.IdmBuiltin_OpenDialog({
            targetModule:[{moduleId:object.option.moduleId}],
            layerOption:object.option
        })
    }else if(object.oType=="close"){
        var targetModule = [];
        if(object.option.moduleId instanceof Array){
            object.option.moduleId.forEach(item=>{
                targetModule.push({moduleId:item})
            })
        }else{
            targetModule.push({moduleId:object.option.moduleId})
        }
        window.IdmBuiltin_CloseDialog({
            targetModule:targetModule,
            layerOption:object.option
        })
    }
    //object.type=="direct" 
    if(object.type=="direct"){
        //
        object.originUrl = location.href;
        //else
        this.findParentLayerHandle(_.cloneDeep(object))
    }
    //iframe()
    if(object.type!="parent"){
        if(window.frames.length>0){
            for (let index = 0; index < window.frames.length; index++) {
                const iframe = window.frames[index];
                if(iframe.IDM){
                    iframe.IDM.broadcast&&iframe.IDM.broadcast.bridgeLayer(_.cloneDeep(object));
                }
            }
        }
    }else{
        //
        this.findParentLayerHandle(_.cloneDeep(object))
    }
}
/**
 * 
 * @param {*} object 
 */
function bridgeLayer(object){
    object.type = "across";
    //isAcrosstrue
    this.callLayerHandle(object)
}
/**
 * callLayerHandle
 * @param {*} object 
 */
function findParentLayerHandle(object){
    object.type = "parent";
    //
    if(parent.location.href!=location.href){
        //
        parent.IDM&&parent.IDM.broadcast.callLayerHandle(object)
    }
}


/**
 * ID
 * @param {
 * url:"=>",
 * param:{
 *  marketModuleId:"IDthis.moduleObject.comId",
 *  pageId:"ID window.IDM.broadcast.pageModule.id ",
 *  packageid:"ID this.moduleObject.packageid "
 * },
 * title:"",
 * showTitle:Booleantruefalse,
 * width:"	string | number	256",
 * height:",  placement  top  bottom 	string | number	256",
 * placement:"	'top' | 'right' | 'bottom' | 'left' | 'center'	'right'",
 * showTop:true
 * success:,
 * yes:,
 * reset:,
 * other:
 * } option 
 */
function openControlSetPanel(option){
    const controlSetPanelId = "ControlSetPanel_"+IDM.UUID();
    option["controlSetPanelId"] = controlSetPanelId;
    this.callControlSetPanelHandle({
        //
        type:"direct",
        oType:"open",
        option
    })
    return controlSetPanelId;
}
/**
 * 
 * @param {
 * controlSetPanelId:"ID"
 * } option 
 */
function closeControlSetPanel(option){
    this.callControlSetPanelHandle({
        //
        type:"direct",
        oType:"close",
        option
    })
}


/**
 * 
 * @param {
 *  type:"direct",
 *  oType:"open  close"
 *  option
 * } object 
 */
function callControlSetPanelHandle(object){
    if(object.oType=="close"){
        //
        window.IdmBuiltin_CloseControlSetPanel({
            controlSetPanelOption:object.option
        })
        //object.type=="direct" 
        if(object.type=="direct"){
            //
            object.originUrl = location.href;
            //else
            this.findParentControlSetPanelHandle(_.cloneDeep(object))
        }
        //iframe()
        if(object.type!="parent"){
            if(window.frames.length>0){
                for (let index = 0; index < window.frames.length; index++) {
                    const iframe = window.frames[index];
                    if(iframe.IDM){
                        iframe.IDM.broadcast&&iframe.IDM.broadcast.bridgeControlSetPanel(_.cloneDeep(object));
                    }
                }
            }
        }else{
            //
            this.findParentControlSetPanelHandle(_.cloneDeep(object))
        }
    }else{
        if(object.option.showTop===false){
            //
            //
            window.IdmBuiltin_OpenControlSetPanel({
                controlSetPanelOption:object.option
            })
            return;
        }
        //
        const isTopPage = this.findParentControlSetPanelHandle(_.cloneDeep(object));
        if(isTopPage){
            //
            window.IdmBuiltin_OpenControlSetPanel({
                controlSetPanelOption:object.option
            })
        }
    }
}
/**
 * 
 * @param {*} object 
 */
function bridgeControlSetPanel(object){
    object.type = "across";
    //isAcrosstrue
    this.callControlSetPanelHandle(object)
}
/**
 * callControlSetPanelHandle
 * @param {*} object 
 */
function findParentControlSetPanelHandle(object){
    object.type = "parent";
    //
    if(parent.location.href!=location.href){
        //
        parent.IDM&&parent.IDM.broadcast.callControlSetPanelHandle(object);
        if(parent.IDM){
            return false;
        }else{
            return true;
        }
    }else{
        return true;
    }
}

export default {
    pageModuleList,
    registerModule,
    unRegisterModule,
    send,
    bridgeSend,
    callModuleReceiveHandle,
    findParentModuleHandle,
    sendChildren,
    sendChildrenInner,
    pageModule:pageModule,
    pageModuleIsLoadComplete:pageModuleIsLoadComplete,
    waitReceiveMessage:waitReceiveMessage,
    waitChildrenReceiveMessage:waitChildrenReceiveMessage,
    pageLoadCompleteAfter:pageLoadCompleteAfter,
    pageModuleLoadCompleteBefore:pageModuleLoadCompleteBefore,
    getModuleContextValue:getModuleContextValue,
    getRangeModule:getRangeModule,
    openDialog,
    closeDialog,
    bridgeLayer,
    callLayerHandle,
    findParentLayerHandle,

    openControlSetPanel,
    closeControlSetPanel,
    callControlSetPanelHandle,
    bridgeControlSetPanel,
    findParentControlSetPanelHandle
}