    &.centerRight {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    &.bottomLeft {
      display: flex;
      align-items: flex-end;
      justify-content: flex-start;
    }
    &.bottomCenter {
      display: flex;
      align-items: flex-end;
      justify-content: center;
    }
    &.bottomRight {
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
    }
  }
  &[hideing="hideing"] {
    animation: idm_dialog_wrap_shade_animation_hide 0.52s;
    /*Safari and Chrome*/
    -webkit-animation: idm_dialog_wrap_shade_animation_hide 0.52s;
