var themeObject="idm-theme-blue"
/**
 * 
 */
function getCurrentThemeInfo(){
    return this.themeObject;
}
/**
 * 
 * @param {*} object 
 */
function setCurrentThemeInfo(object){
    this.themeObject = object;
    //
    var classList = $("body").attr("class");
    if(classList){
        classList = classList.split(" ");
        var newClassNames = []
        classList.forEach(element => {
            if(!element.startsWiths(IDM.setting.applications.themeNamePrefix)){
                newClassNames.push(element)
            }
        });
        $("body").attr("class",newClassNames.join(" "));
    }
    //bodyclass
    $("body").addClass(IDM.setting.applications.themeNamePrefix+(typeof object==="object"?JSON.stringify(object):object));
}

export default {
    getCurrentThemeInfo,
    setCurrentThemeInfo,
    themeObject:themeObject
}