//
var pageObject={
    //
    pullToRefresh:true,
    //
    pullToRefreshPtr:null
}
/**
 * 
 */
function getPullToRefreshStatus(){
    return this.pageObject.pullToRefresh;
}
/**
 * 
 * @param {*} object 
 */
function stopPullToRefresh(){
    this.pageObject.pullToRefresh = false;
}
/**
 * 
 * @param {*} object 
 */
function startPullToRefresh(ptr){
    this.pageObject.pullToRefresh = true;
    if(ptr){
        this.pageObject.pullToRefreshPtr = ptr;
    }
}
/**
 * ptr
 */
function getPullToRefreshPtr(){
    return this.pageObject.pullToRefreshPtr;
}
/**
 * 
 * @param {
 * routerId:"ID",
 * status: true    false  ,
 * save: true  status false ,
 * } ctx 
 */
function changePageUserDefinedStatus(ctx){
   return window.IdmBuiltin_changePageUserDefinedStatus(ctx);
}
/**
 * 
 * @param {routerId:"ID"} ctx 
 * @returns 
 */
function executeUserDefinedSave(ctx){
    return window.IdmBuiltin_executeUserDefinedEvent(ctx,"save");
}
/**
 * 
 * @param {routerId:"ID"} ctx 
 * @returns 
 */
function executeUserDefinedReset(ctx){
    return window.IdmBuiltin_executeUserDefinedEvent(ctx,"reset");
}
/**
 * 
 * @param {routerId:"ID"} ctx 
 * @returns 
 */
function executeUserDefinedQuit(ctx){
    return window.IdmBuiltin_executeUserDefinedEvent(ctx,"quit");
}
/**
 * 
 * @param {routerId:"ID",pageid:"ID",moveIndex:"",targetIndex:""} ctx 
 * @returns 
 */
function executeComponentMoveIndex(ctx){
    return window.IdmBuiltin_executeComponentMoveIndexEvent(ctx);
}

/**
 * 
 * @param {*} item 
 */
function checkComponentHasAuthority(item){
    let showComponent = true;
    //openAuthorize&&privilege&&role&&dept&&user
    let attrConfig = IDM.setting.applications.basicAttributeAuthorizeConfig;
    let openAuthorize = attrConfig.openAuthorize;
    if(item.props&&item.props.basicAttr&&item.props.basicAttr.showType=="show"||(item.againLoad===true&&item.props&&item.props.basicAttr&&item.props.basicAttr.showType!="role")){
    //   console.log(" ~ file: Page.vue ~ line 1888 ~ moduleData.forEach ~ item.againLoad", item.againLoad)
      //
      
    }else if(item.props&&item.props.basicAttr&&item.props.basicAttr.showType=="hide"){
      showComponent = false;
    }else if(item.props&&item.props.basicAttr&&item.props.basicAttr.showType=="custom"&&item.props.basicAttr.customShowEvent){
      //
      const customArray = IDM.invokeCustomFunctions.apply(this, [item.props.basicAttr.customShowEvent, {}]);
      if(customArray&&customArray.length){
        showComponent = customArray[0];
      }
    }else if(openAuthorize===true){
      let privilege = attrConfig.privilege||{},privilegePass=-1,
      role = attrConfig.role||{},rolePass = -1,
      dept = attrConfig.dept||{},deptPass = -1,
      user = attrConfig.user||{},userPass = -1;
      //
      if(privilege.open===true&&privilege.comparisonObject&&item.props&&item.props.basicAttr&&item.props.basicAttr.privilegeList&&item.props.basicAttr.privilegeList.length){
        let privilegeComparisonObject = IDM.express.replace("@["+privilege.comparisonObject+"]",{IDM:IDM,window});//,...this.pageInterfaceResultObject
        if(privilege.separator&&IDM.type(privilegeComparisonObject)!='array'){
          privilegeComparisonObject = privilegeComparisonObject.split(privilege.separator);
        }
        //falsetrue
        privilegePass = false;
        //
        item.props.basicAttr.privilegeList.forEach(iitem=>{
          let comparisonKey = IDM.express.replace("@["+(privilege.comparisonKey||'key')+"]",iitem);
          if(privilegeComparisonObject===undefined||privilegeComparisonObject.indexOf(comparisonKey)>-1){
            privilegePass = true;
          }
        })
      }
      //
      if(role.open===true&&role.comparisonObject&&item.props&&item.props.basicAttr&&item.props.basicAttr.roleList&&item.props.basicAttr.roleList.length){
        let roleComparisonObject = IDM.express.replace("@["+role.comparisonObject+"]",{IDM:IDM,window});//,...this.pageInterfaceResultObject
        if(role.separator&&IDM.type(roleComparisonObject)!='array'){
          roleComparisonObject = roleComparisonObject.split(role.separator);
        }
        //falsetrue
        rolePass = false;
        //
        item.props.basicAttr.roleList.forEach(iitem=>{
          let comparisonKey = IDM.express.replace("@["+(role.comparisonKey||'key')+"]",iitem);
          if(roleComparisonObject===undefined||roleComparisonObject.indexOf(comparisonKey)>-1){
            rolePass = true;
          }
        })
      }
      //
      if(dept.open===true&&dept.comparisonObject&&item.props&&item.props.basicAttr&&item.props.basicAttr.deptList&&item.props.basicAttr.deptList.length){
        let deptComparisonObject = IDM.express.replace("@["+dept.comparisonObject+"]",{IDM:IDM,window});//,...this.pageInterfaceResultObject
        if(dept.separator&&IDM.type(deptComparisonObject)!='array'){
          deptComparisonObject = deptComparisonObject.split(dept.separator);
        }
        //falsetrue
        deptPass = false;
        //
        item.props.basicAttr.deptList.forEach(iitem=>{
          let comparisonKey = IDM.express.replace("@["+(dept.comparisonKey||'key')+"]",iitem);
          if(deptComparisonObject===undefined||deptComparisonObject.indexOf(comparisonKey)>-1){
            deptPass = true;
          }
        })
      }
      //
      if(dept.open===true&&dept.comparisonObject&&item.props&&item.props.basicAttr&&item.props.basicAttr.deptColumns&&item.props.basicAttr.deptColumns.length){
        let deptComparisonObject = IDM.express.replace("@["+dept.comparisonObject+"]",{IDM:IDM,window});//,...this.pageInterfaceResultObject
        if(dept.separator&&IDM.type(deptComparisonObject)!='array'){
          deptComparisonObject = deptComparisonObject.split(dept.separator);
        }
        //falsetrue
        deptPass = false;
        if(deptComparisonObject===undefined){
          deptPass = true;
        }else{
          //
          item.props.basicAttr.deptColumns.forEach(iitem=>{
            //
            let deptList = iitem.deptList,includeChild=iitem.includeChild;
            deptList&&deptList.forEach(deptItem=>{
              let comparisonKey = IDM.express.replace("@["+(dept.comparisonKey||'key')+"]",deptItem);
              if((includeChild=="0"&&IDM.type(deptComparisonObject)=="array"&&deptComparisonObject[deptComparisonObject.length-1]==comparisonKey)||
              (includeChild=="1"&&deptComparisonObject.indexOf(comparisonKey)>-1)
              ){
                deptPass = true;
              }
            })
          })
        }
      }
      //
      if(user.open===true&&user.comparisonObject&&item.props&&item.props.basicAttr&&item.props.basicAttr.userList&&item.props.basicAttr.userList.length){
        let userComparisonObject = IDM.express.replace("@["+user.comparisonObject+"]",{IDM:IDM,window});//,...this.pageInterfaceResultObject
        //falsetrue
        userPass = false;
        //
        let userArray = item.props.basicAttr.userList.map(item=>{
          let comparisonKey = IDM.express.replace("@["+(user.comparisonKey||'key')+"]",item);
          return comparisonKey
        });
        if(userArray.indexOf(userComparisonObject)>-1){
          userPass = true;
        }
      }
      //
      if(!((privilegePass===-1&&rolePass===-1&&deptPass===-1&&userPass===-1)||(privilegePass!==-1&&privilegePass)||(rolePass!==-1&&rolePass)||(deptPass!==-1&&deptPass)||(userPass!==-1&&userPass))){
        showComponent = false;
      }
    }
    return showComponent;
}

export default {
    getPullToRefreshStatus,
    getPullToRefreshPtr,
    stopPullToRefresh,
    startPullToRefresh,
    pageObject:pageObject,
    changePageUserDefinedStatus,
    executeUserDefinedSave,
    executeUserDefinedReset,
    executeUserDefinedQuit,
    checkComponentHasAuthority,
    executeComponentMoveIndex
}