/**
 * ID
 * @param {ID} dataSourceId 
 * @param {} success 
 * @param {} error 
 * @param {} finallyCb 
 */
function getConfig(dataSourceId, success, error, finallyCb = () => {}) {
    if (!IDM.setting.api.dataSourceFetchListApi && !IDM.setting.mockurl.dataSourceFetchListApi) {
        IDM.message.warning("setting.js[IDM.setting.api.dataSourceFetchListApi]")
        return;
    }
    IDM.http
        .get(IDM.setting.api.dataSourceFetchListApi || IDM.setting.mockurl.dataSourceFetchListApi, {
            dataSourceId: dataSourceId
        })
        .then(success)
        .catch(error)
        .finally(finallyCb);
}
/**
 * 
 * @param {ID} dataSourceId 
 * @param {moduleObject:{},param:{}} paramInfo 
 * @param {} successCallback 
 * @param {} errorCallback
 * @param {} finallyCallback
 */
function request(dataSourceId, paramInfo, successCallback, errorCallback, finallyCallback = () => {}) {
    return new Promise((resolve, reject) => {
        if (!dataSourceId) {
            IDM.message.error("ID")
            if(errorCallback) {
                errorCallback({
                    type: 'error',
                    message: 'ID'
                })
            }
            reject('ID')
            finallyCallback()
            return;
        }
        this.getConfig(dataSourceId, function (dsRes) {
            var dataSourceRes = dsRes && dsRes.data;
            if (dataSourceRes.code == "200") {
                //
                var dataSourceData = dataSourceRes.data;
                var resultData;
                switch (dataSourceData.type) {
                    case 1:
                        //1API
                        //
                        var httpApi = dataSourceData.loadType == 1 ? IDM.setting.api.dataSourceForwardApi : dataSourceData.api;
                        
                        if(!httpApi){
                            IDM.message.warning(dataSourceData.loadType == 1?"setting.js[IDM.setting.api.dataSourceForwardApi]":"")
                            return false;
                        }
                        var httpParam = {}, httpHeader = {};
                        if (dataSourceData.paramJson) {
                            try {
                                //[{\"name\":\"1\",\"valueType\":1,\"value\":true},{\"name\":\"1\",\"valueType\":0,\"value\":\"3\"}]
                                var paramJSON = JSON.parse(dataSourceData.paramJson);
                                for (let index = 0; index < paramJSON.length; index++) {
                                    var paramObject = paramJSON[index];
                                    var paramValue = "";
                                    switch (paramObject.valueType) {
                                        case 0://
                                        case 1://
                                            paramValue = paramObject.value;
                                            break;
                                        case 2://
                                            var replaveValue = {};
                                            if (paramInfo) {
                                                replaveValue = { ...paramInfo };
                                                if (paramInfo.param && IDM.type(paramInfo.param) == "object") {
                                                    IDM.mix(replaveValue, paramInfo.param);
                                                }
                                            }
                                            replaveValue["IDM"] = IDM;
                                            paramValue = IDM.express.replace(paramObject.value, replaveValue, true)
                                            break;
                                    }
                                    httpParam[paramObject.name] = paramValue;
                                }
                            } catch (error) {
    
                            }
                        }
                        if (dataSourceData.headerJson) {
                            try {
                                //[{\"name\":\"1\",\"valueType\":1,\"value\":true},{\"name\":\"1\",\"valueType\":0,\"value\":\"3\"}]
                                var paramJSON = JSON.parse(dataSourceData.headerJson);
                                for (let index = 0; index < paramJSON.length; index++) {
                                    var paramObject = paramJSON[index];
                                    var paramValue = "";
                                    switch (paramObject.valueType) {
                                        case 0://
                                        case 1://
                                            paramValue = paramObject.value;
                                            break;
                                        case 2://
                                            var replaveValue = {};
                                            if (paramInfo) {
                                                replaveValue = { ...paramInfo };
                                                if (paramInfo.param && IDM.type(paramInfo.param) == "object") {
                                                    IDM.mix(replaveValue, paramInfo.param);
                                                }
                                            }
                                            replaveValue["IDM"] = IDM;
                                            paramValue = IDM.express.replace(paramObject.value, replaveValue, true)
                                            break;
                                    }
                                    httpHeader[paramObject.name] = paramValue;
                                }
                            } catch (error) {
    
                            }
                        }
                        //
                        if(dataSourceData.functionParam){
                            try {
                                var fun = IDM.express.eval(dataSourceData.functionParam);
                                httpParam = fun(httpParam);
                            } catch (error) {
        
                            }
                        }
                        var optionObject = {};
                        if(dataSourceData.loadType == 1){
                            //
                            httpParam={
                                paramJSON:httpParam,
                                headerJSON:httpHeader,
                                dataSourceData
                            }
                            optionObject["headers"]={
                                "Content-Type": "application/json;charset=UTF-8"
                            };
                        }else{
                            optionObject["headers"] = httpHeader;
                            optionObject["timeout"] = dataSourceData.timeout || 0;
                            optionObject["crossDomain"] = dataSourceData.crossOrigin === 0 ? false : true;
                        }
    
    
                        IDM.http[dataSourceData.requestType == "GET"&&dataSourceData.loadType != 1 ? "get" : "post"](httpApi, httpParam, optionObject).then(function (dataRes) {
                            resultData = dataRes;
                            if (dataSourceData.functionResult) {
                                //
                                try {
                                    var fun = IDM.express.eval(dataSourceData.functionResult);
                                    resultData = fun(resultData);
                                } catch (error) {
                                    console.error(error);
                                }
                            }
                            if (successCallback) {
                                successCallback(resultData)
                            }
                            resolve(resultData)
                        }).catch(function (dataError) {
                            if (errorCallback) {
                                errorCallback(dataError)
                            }
                            if (dataSourceData.functionError) {
                                try {
                                    var fun = IDM.express.eval(dataSourceData.functionError);
                                    fun(dataError);
                                } catch (error) {
                                    console.error(error);
                                }
                            } else {
                                IDM.message.error("" + dataSourceData.title + "" + dataError.message)
                            }
                            reject(dataError)
                        }).finally(finallyCallback)
    
                        break;
                    case 2:
                    //2
                    case 3:
                    //3json
                    case 5:
                        //5csv
                        //2resultJson35fileContent
                        resultData = dataSourceData.type == 2 ? dataSourceData.resultJson : dataSourceData.fileContent;
                        if(dataSourceData.type != 5){
                            try {
                                resultData = JSON.parse(resultData);
                            } catch (error) {
                                
                            }
                        }
                        if (dataSourceData.functionResult) {
                            //
                            try {
                                var fun = IDM.express.eval(dataSourceData.functionResult);
                                resultData = fun(resultData);
                            } catch (error) {
                                console.error(error);
                            }
                        }
                        if (successCallback) {
                            successCallback(resultData)
                        }
                        resolve(resultData)
                        finallyCallback()
                        break;
                    case 4:
                        //4sql
                        if(!IDM.setting.api.dataSourceDbSqlApi){
                            IDM.message.warning("sqlsetting.js[IDM.setting.api.dataSourceDbSqlApi]")
                            return;
                        }
                        var sqlParam = {};
                        if (dataSourceData.paramJson) {
                            try {
                                //[{\"name\":\"1\",\"valueType\":1,\"value\":true},{\"name\":\"1\",\"valueType\":0,\"value\":\"3\"}]
                                var paramJSON = JSON.parse(dataSourceData.paramJson);
                                for (let index = 0; index < paramJSON.length; index++) {
                                    var paramObject = paramJSON[index];
                                    var paramValue = "";
                                    switch (paramObject.valueType) {
                                        case 0://
                                        case 1://
                                            paramValue = paramObject.value;
                                            break;
                                        case 2://
                                            var replaveValue = {};
                                            if (paramInfo) {
                                                replaveValue = { ...paramInfo };
                                                if (paramInfo.param && IDM.type(paramInfo.param) == "object") {
                                                    IDM.mix(replaveValue, paramInfo.param);
                                                }
                                            }
                                            replaveValue["IDM"] = IDM;
                                            paramValue = IDM.express.replace(paramObject.value, replaveValue, true)
                                            break;
                                    }
                                    sqlParam[paramObject.name] = paramValue;
                                }
                            } catch (error) {
    
                            }
                        }
                        //
                        if(dataSourceData.functionParam){
                            try {
                                var fun = IDM.express.eval(dataSourceData.functionParam);
                                sqlParam = fun(sqlParam);
                            } catch (error) {
        
                            }
                        }
                        paramInfo.moduleObject?delete paramInfo.moduleObject:null;
                        //ID
                        IDM.http.post(IDM.setting.api.dataSourceDbSqlApi, {
                            dataSourceId,
                            paramInfo:paramInfo,
                            sqlParam
                        }, {
                            headers:{
                                "Content-Type": "application/json;charset=UTF-8"
                            }
                        }).then(function (dataRes) {
                            try {
                                resultData = dataRes;
                                if (dataSourceData.functionResult) {
                                    //
                                    try {
                                        var fun = IDM.express.eval(dataSourceData.functionResult);
                                        resultData = fun(resultData);
                                    } catch (error) {
                                        console.error(error);
                                    }
                                }
                                if (successCallback) {
                                    successCallback(resultData)
                                }
                                resolve(resultData)
                            } catch (error) {
                                console.error("",error);
                                reject(error)
                            }
                        }).catch(function (dataError) {
                            if (errorCallback) {
                                errorCallback(dataError)
                            }
                            if (dataSourceData.functionError) {
                                try {
                                    var fun = IDM.express.eval(dataSourceData.functionError);
                                    fun(dataError);
                                } catch (error) {
                                    console.error(error);
                                }
                            } else {
                                IDM.message.error("" + dataSourceData.title + "" + dataError.message)
                            }
                            reject(dataError)
                        }).finally(finallyCallback)
                        break;
                    case 6:
                        var httpApi = IDM.setting.api.dataModelGetDataApi;
                        
                        if(!httpApi){
                            IDM.message.warning("setting.js[IDM.setting.api.dataModelGetDataApi]")
                            reject()
                            return false;
                        }
                        debugger
                        IDM.http.get(httpApi, {
                            id:dataSourceData.datamodelId
                        }).then(function (dataRes) {
                            resultData = dataRes;
                            if (dataSourceData.functionResult) {
                                //
                                try {
                                    var fun = IDM.express.eval(dataSourceData.functionResult);
                                    resultData = fun(resultData);
                                } catch (error) {
                                    console.error(error);
                                }
                            }
                            if (successCallback) {
                                successCallback(resultData)
                            }
                            resolve(resultData)
                        }).catch(function (dataError) {
                            if (errorCallback) {
                                errorCallback(dataError)
                            }
                            if (dataSourceData.functionError) {
                                try {
                                    var fun = IDM.express.eval(dataSourceData.functionError);
                                    fun(dataError);
                                } catch (error) {
                                    console.error(error);
                                }
                            } else {
                                IDM.message.error("" + dataSourceData.title + "" + dataError.message)
                            }
                            reject(dataError)
                        }).finally(finallyCallback)
                    break;
                }
            } else {
                IDM.message.error(dataSourceRes.message)
            }
        }, function (error) {
            if (errorCallback) {
                errorCallback(error)
            } else {
                IDM.message.error("" + error.message)
            }
            reject(error)
        })
    })
}
/**
 * 
 * @param {} dataSourceObject 
 * @param {} successCallback 
 * @param {} errorCallback 
 */
function debug(dataSourceObject,successCallback, errorCallback){
    var resultData;
    switch (dataSourceObject.type) {
        case "1":
            //1API
            var httpApi = dataSourceObject.loadType == 1 ? IDM.setting.api.dataSourceForwardApi : dataSourceObject.api;
            
            if(!httpApi){
                IDM.message.warning(dataSourceObject.loadType == 1?"setting.js[IDM.setting.api.dataSourceForwardApi]":"")
                return false;
            }
            var httpParam = {}, httpHeader = {};
            if (dataSourceObject.paramJson_debug) {
                try {
                    //[{\"name\":\"1\",\"valueType\":1,\"value\":true},{\"name\":\"1\",\"valueType\":0,\"value\":\"3\"}]
                    var paramJSON = dataSourceObject.paramJson_debug;
                    for (let index = 0; index < paramJSON.length; index++) {
                        var paramObject = paramJSON[index];
                        var paramValue = "";
                        switch (paramObject.valueType) {
                            case 0://
                            case 1://
                            case 2://
                                paramValue = paramObject.value;
                                break;
                        }
                        httpParam[paramObject.name] = paramValue;
                    }
                } catch (error) {

                }
            }
            if (dataSourceObject.headerJson_debug) {
                try {
                    //[{\"name\":\"1\",\"valueType\":1,\"value\":true},{\"name\":\"1\",\"valueType\":0,\"value\":\"3\"}]
                    var paramJSON = dataSourceObject.headerJson_debug;
                    for (let index = 0; index < paramJSON.length; index++) {
                        var paramObject = paramJSON[index];
                        var paramValue = "";
                        switch (paramObject.valueType) {
                            case 0://
                            case 1://
                            case 2://
                                paramValue = paramObject.value;
                                break;
                        }
                        httpHeader[paramObject.name] = paramValue;
                    }
                } catch (error) {

                }
            }
            //
            if(dataSourceObject.functionParam){
                try {
                    var fun = IDM.express.eval(dataSourceObject.functionParam);
                    httpParam = fun(httpParam);
                } catch (error) {

                }
            }
            var optionObject = {};
            if(dataSourceObject.loadType == 1){
                //
                httpParam={
                    paramJSON:httpParam,
                    headerJSON:httpHeader,
                    dataSourceData:dataSourceObject
                }
                optionObject["headers"]={
                    "Content-Type": "application/json;charset=UTF-8"
                };
            }else{
                optionObject["headers"] = httpHeader;
                optionObject["timeout"] = dataSourceObject.timeout || 0;
                optionObject["crossDomain"] = dataSourceObject.crossOrigin === 0 ? false : true;
            }

            IDM.http[dataSourceObject.requestType == "GET"&&dataSourceObject.loadType != 1 ? "get" : "post"](httpApi, httpParam, optionObject).then(function (dataRes) {
                resultData = dataRes;
                if (dataSourceObject.functionResult) {
                    //
                    try {
                        var fun = IDM.express.eval(dataSourceObject.functionResult);
                        resultData = fun(resultData);
                    } catch (error) {
                        console.error(error);
                    }
                }
                if (successCallback) {
                    successCallback(resultData)
                }
            }).catch(function (dataError) {
                if (errorCallback) {
                    errorCallback(dataError)
                }
                if (dataSourceObject.functionError) {
                    try {
                        var fun = IDM.express.eval(dataSourceObject.functionError);
                        fun(dataError);
                    } catch (error) {
                        console.error(error);
                    }
                }
            })
            break;
        case "4":
            //4sql
            if(!IDM.setting.api.dataSourceDbSqlApi){
                IDM.message.warning("sqlsetting.js[IDM.setting.api.dataSourceDbSqlApi]")
                return false;
            }
            if(!dataSourceObject.dataSourceId){
                IDM.message.warning("")
                return false;
            }
            var sqlParam = {};
            if (dataSourceObject.paramJson_debug) {
                try {
                    //[{\"name\":\"1\",\"valueType\":1,\"value\":true},{\"name\":\"1\",\"valueType\":0,\"value\":\"3\"}]
                    var paramJSON = dataSourceObject.paramJson_debug;
                    for (let index = 0; index < paramJSON.length; index++) {
                        var paramObject = paramJSON[index];
                        var paramValue = "";
                        switch (paramObject.valueType) {
                            case 0://
                            case 1://
                            case 2://
                                paramValue = paramObject.value;
                                break;
                        }
                        sqlParam[paramObject.name] = paramValue;
                    }
                } catch (error) {

                }
            }
            //
            if(dataSourceObject.functionParam){
                try {
                    var fun = IDM.express.eval(dataSourceObject.functionParam);
                    sqlParam = fun(sqlParam);
                } catch (error) {

                }
            }
            //ID
            IDM.http.post(IDM.setting.api.dataSourceDbSqlApi, {
                dataSourceId:dataSourceObject.dataSourceId,
                sqlParam
            }, {
                headers:{
                    "Content-Type": "application/json;charset=UTF-8"
                }
            }).then(function (dataRes) {
                resultData = dataRes;
                if (dataSourceObject.functionResult) {
                    //
                    try {
                        var fun = IDM.express.eval(dataSourceObject.functionResult);
                        resultData = fun(resultData);
                    } catch (error) {
                        console.error(error);
                    }
                }
                if (successCallback) {
                    successCallback(resultData)
                }
            }).catch(function (dataError) {
                if (errorCallback) {
                    errorCallback(dataError)
                }
                if (dataSourceObject.functionError) {
                    try {
                        var fun = IDM.express.eval(dataSourceObject.functionError);
                        fun(dataError);
                    } catch (error) {
                        console.error(error);
                    }
                }
            })
            break;
        case "2":
            //2
        case "3":
            //3json
        case "5":
            //5csv
            //2resultJson35fileContent
            resultData = dataSourceObject.type == 2 ? dataSourceObject.resultJson : dataSourceObject.fileContent;
            if(dataSourceObject.type != 2){
                //
                if(dataSourceObject.file_name&&dataSourceObject.file_name.length){
                    resultData = dataSourceObject.file_name[dataSourceObject.file_name.length-1].content;
                }
            }
            if(dataSourceObject.type != 5){
                try {
                    resultData = JSON.parse(resultData);
                } catch (error) {
                    if (errorCallback) {
                        errorCallback(error)
                    }
                }
            }
            if (dataSourceObject.functionResult) {
                //
                try {
                    var fun = IDM.express.eval(dataSourceObject.functionResult);
                    resultData = fun(resultData);
                } catch (error) {
                    console.error(error);
                }
            }
            if (successCallback) {
                successCallback(resultData)
            }
            break;
        case "6":
            var httpApi = IDM.setting.api.dataModelGetDataApi;
            
            if(!httpApi){
                IDM.message.warning("setting.js[IDM.setting.api.dataModelGetDataApi]")
                return false;
            }
            IDM.http.get(httpApi, {
                id:dataSourceObject.datamodelId
            }).then(function (dataRes) {
                resultData = dataRes;
                if (dataSourceObject.functionResult) {
                    //
                    try {
                        var fun = IDM.express.eval(dataSourceObject.functionResult);
                        resultData = fun(resultData);
                    } catch (error) {
                        console.error(error);
                    }
                }
                if (successCallback) {
                    successCallback(resultData)
                }
            }).catch(function (dataError) {
                if (errorCallback) {
                    errorCallback(dataError)
                }
                if (dataSourceObject.functionError) {
                    try {
                        var fun = IDM.express.eval(dataSourceObject.functionError);
                        fun(dataError);
                    } catch (error) {
                        console.error(error);
                    }
                }
            })
        break;
    }
}
export default {
    request,
    getConfig,
    debug
}