<template>
  <!---->
  <div
    :id="`router_page_${routerId}`"
    :style="{ zIndex: index, ...styleObject, ...containerStyleObject }"
    :data-pageid="pageid"
    class="idm_render_engine_page_container idm_page_box"
    :class="
      IDM.setting.applications.themeNamePrefix +
      (typeof themeSetObject === 'object'
        ? JSON.stringify(themeSetObject)
        : themeSetObject)
    "
  >
    <div class="idm_render_engine_page_content" :style="contentStyleObject">
      <!---->
      <div
        :id="`idm_dialog_wrap_${routerId}`"
        v-show="dialogVisibleStatus"
        class="idm_dialog_wrap_preview"
        :hideing="`${dialogIsHideing ? 'hideing' : ''}`"
      >
        <div
          class="idm_dialog_shade_layer"
          :class="'idm_dialog_shade_layer_' + item.packageid"
          v-for="(item, index) in dialogObjectList"
          :key="index"
          :dialog-id="item.packageid"
          @click="layerClickHandle(item, $event)"
        ></div>
      </div>
      <!---->
      <div id="idm_hover_layer_wrap" class="idm_hover_layer_wrap_preview"></div>
      <div
        class="idm_page_root"
        :id="'PAGE_' + PageSettingData.id + '_' + routerId"
        :idm-com-anchor-id="PageSettingData.id"
        ref="idm_page_root"
        :class="`${PageSettingData.id}`"
      ></div>
    </div>
    <transition name="idm_page_user_defined_topbar">
      <div
        v-if="pageUserDefinedStatus && hiddenCustomizationToolBar"
        class="idm_page_user_defined_topbar"
      >
