    /**
     * moveModuleIdpackageid
     * tragetModuleIdpackageidpositioninnerid
     * positiontopbottominner
     *
     */
    changePageSettingModuleSort(moveModuleId, tragetModuleId, position) {
      //
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      //
      /**
       * 
       * [
       *  {
       *  moduleId:packageid,
       *  moduleName:asName
       *  }
       * ]
       */
      const customContainerModuleId =
        propData.userCustomizationModuleContainer &&
        propData.userCustomizationModuleContainer.length > 0
          ? propData.userCustomizationModuleContainer[0].moduleId
          : "";
      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if (!customModuleObject) {
        return false;
      }
      let moveModuleIndex = -1,
        tragetModuleIndex = -1;
      customModuleObject.children &&
        customModuleObject.children.forEach((item, index) => {
          if (item.packageid == moveModuleId && position == "inner") {
            item.containerIndex = tragetModuleId;
            if (item.relationType && item.relationType.startsWiths("inner_")) {
              item.relationType = "inner_" + tragetModuleId;
            }
          }
          if (item.packageid == moveModuleId) {
            moveModuleIndex = index;
          }
          if (item.packageid == tragetModuleId) {
            tragetModuleIndex = index;
          }
        });
      if (tragetModuleIndex > -1 && moveModuleIndex > -1 && position != "inner") {
        customModuleObject.children[moveModuleIndex].containerIndex =
          customModuleObject.children[tragetModuleIndex].containerIndex;
        customModuleObject.children[moveModuleIndex].relationType = position;
        customModuleObject.children[moveModuleIndex].relationId = tragetModuleId;

        customModuleObject.children.splice(
          tragetModuleIndex + (position == "bottom" ? 1 : 0),
          0,
          customModuleObject.children[moveModuleIndex]
        );
        customModuleObject.children.splice(
          moveModuleIndex + (tragetModuleIndex < moveModuleIndex ? 1 : 0),
          1
        );
      }
    },

    /**
     * 
     */
    removeUserDefinedSelect(item) {
      let that = this;
      item.hidden = true;
      let lsmodule = that.getModuleTree(
        this.PageSettingData.page.layout.children,
        item.packageid
      );
      lsmodule ? (lsmodule.hidden = true) : null;
      that.$forceUpdate();
      $(
        document
          .querySelector(`#router_page_${that.routerId}`)
          .querySelector(`#${item.packageid}`)
      ).remove();
    },
    /**
     * 
     */
    addUserDefinedSelect(item) {
      //
      let that = this;
      item.hidden = false;
      let lsmodule = that.getModuleTree(
        this.PageSettingData.page.layout.children,
        item.packageid
      );
      //
      if (!lsmodule) {
        let propData = this.PageSettingData.page.layout.props.compositeAttr;
        const customContainerModuleId =
          propData.userCustomizationModuleContainer &&
          propData.userCustomizationModuleContainer.length > 0
            ? propData.userCustomizationModuleContainer[0].moduleId
            : "";
        let customContainerModule = that.getModuleTree(
          this.PageSettingData.page.layout.children,
          customContainerModuleId
        );
        lsmodule = _.cloneDeep(item);
        customContainerModule.children.push(lsmodule);
      }

      lsmodule ? (lsmodule.hidden = false) : null;
      that.$forceUpdate();
      //
      that.moduleReload(item.packageid);
      //
      //
      let lastModule;
      this.customModuleObject.children &&
        this.customModuleObject.children.forEach((module) => {
          if (
            module.containerIndex == lsmodule.containerIndex &&
            module.hidden !== true
          ) {
            lastModule = module;
          }
        });
      //
      if (lastModule) {
        that.changePageSettingModuleSort(item.packageid, lastModule.packageid, "bottom");
      } else {
        that.changePageSettingModuleSort(
          item.packageid,
          lsmodule.containerIndex,
          "inner"
        );
      }
      if (
        lsmodule &&
        this.customModuleObject.children?.filter(
          (ftem) => ftem.packageid == lsmodule.packageid
        ).length == 0
      ) {
        this.customModuleObject.children.push(lsmodule);
      }
      //
      that.registerModuleMouseDownEvent(item.packageid);
    },
    /**
     * 
     * item:
     * type:replace  changeseats add 
     */
    userDefinedModuleReplace(item, type) {
      let that = this;
      let orgcontainerIndex = that.currentContainerShowModuleContainerIndex;
      if (that.currentContainerShowModuleObject) {
        orgcontainerIndex = that.currentContainerShowModuleObject.containerIndex;
        //type=replace 999999999
        that.currentContainerShowModuleObject.containerIndex = item.containerIndex;
        that.currentContainerShowModuleObject.relationType = item.relationType;
        that.currentContainerShowModuleObject.relationId = item.relationId;
      }
      //
      that.changePageSettingModuleSort(item.packageid, orgcontainerIndex, "inner");
      item.relationType = "inner_" + orgcontainerIndex;

      if (type == "changeseats") {
        //
        $(
          document
            .querySelector(`#router_page_${that.routerId}`)
            .querySelector(`#${item.packageid}`)
        ).remove();
      }
      //
      that.currentContainerShowModuleObject &&
        $(
          document
            .querySelector(`#router_page_${that.routerId}`)
            .querySelector(`#${that.currentContainerShowModuleObject.packageid}`)
        ).remove();
      //
      that.moduleReload(item.packageid);
      //
      that.registerModuleMouseDownEvent(item.packageid);
      if (type == "changeseats") {
        //
        that.moduleReload(that.currentContainerShowModuleObject.packageid);
        //
        that.registerModuleMouseDownEvent(
          that.currentContainerShowModuleObject.packageid
        );
      }
      that.pageUserDefinedModalVisible = false;
      //
      IDM.broadcast.send({
        type: "resendAutoLayout",
        rangeModule: "",
        message: {},
      });
    },
    /**
     * 
     */
    userDefinedSave() {
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;

      if (
        propData.customizationToolBarSaveEvent &&
        propData.customizationToolBarSaveEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarSaveEvent",
          {
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
          }
        );
        if (resultBack === false) {
          return;
        }
      }
      that.userDefinedSaveLoading = true;

      const customContainerModuleId =
        propData.userCustomizationModuleContainer &&
        propData.userCustomizationModuleContainer.length > 0
          ? propData.userCustomizationModuleContainer[0].moduleId
          : "";

      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if (!customModuleObject) {
        that.userDefinedSaveLoading = false;
        return false;
      }

      let saveNewData = _.cloneDeep(customModuleObject);
      this.deleteLayoutAttachAttr(saveNewData);
      //
      propData.userCustomizationSaveUrl &&
        IDM.http
          .post(propData.userCustomizationSaveUrl, {
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
            customData: JSON.stringify(saveNewData),
          })
          .then((res) => {
            res = res ? res.data : {};
            that.userDefinedSaveLoading = false;
            if (res.code !== "200") {
              IDM.message.error("message" + res.message);
              return;
            }
            IDM.message.success("");
            //
            IDM.page.changePageUserDefinedStatus({
              routerId: that.routerId,
              status: false,
            });
          })
          .catch((error) => {
            IDM.message.error("message" + error.message);
            that.userDefinedSaveLoading = false;
          });
    },
    /**
     * 
     */
    userDefinedReset() {
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;

      if (
        propData.customizationToolBarResetEvent &&
        propData.customizationToolBarResetEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarResetEvent",
          {
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
          }
        );
        if (resultBack === false) {
          return;
        }
      }

      that.userDefinedSaveLoading = true;

      //
      propData.userCustomizationSaveUrl &&
        IDM.http
          .post(propData.userCustomizationSaveUrl, {
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
            reset: 1,
          })
          .then((res) => {
            res = res ? res.data : {};
            if (res.code !== "200") {
              that.userDefinedSaveLoading = false;
              IDM.message.error("message" + res.message);
              return;
            }
            IDM.message.success("3");
            setTimeout(() => {
              window.location.reload();
            }, 3000);
          })
          .catch((error) => {
            IDM.message.error("message" + error.message);
            that.userDefinedSaveLoading = false;
          });
    },
    /**
     * 
     */
    userDefinedQuit() {
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (
        propData.customizationToolBarQuitEvent &&
        propData.customizationToolBarQuitEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarQuitEvent",
          {
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
          }
        );
        if (resultBack === false) {
          return;
        }
      }
      //
      if (this.isChangeUserDefined === true) {
        window.location.reload();
      } else {
        IDM.page.changePageUserDefinedStatus({ routerId: this.routerId, status: false });
      }
    },
    /**
     * 
     */
    layerClickHandle(item, e) {
      let dialogId = item ? item.packageid : "";
      if (!dialogId) {
        return;
      }
      //shadeClose
      if (
        item &&
        item.props &&
        item.props.compositeAttr &&
        item.props.compositeAttr.shadeClose === false
      ) {
        return;
      }
      window.IdmBuiltin_CloseDialog({
        routerId: this.routerId,
        targetModule: [{ moduleId: dialogId }],
      });
    },
    /**
     * react
     */
    async initReactOrOtherLib(react) {
      //
      if (react && !IDM.router.getReact()) {
        IDM.router.setReact();
        //react
        await IDM.http
          .importFiles([
            IDM.url.getWebPath("~/lib/react.min.js"),
            IDM.url.getWebPath("~/lib/react-dom.min.js"),
          ])
          .then(() => {
            //
            console.log("react");
          })
          .catch((err) => {
            console.log("react");
          });
      }
    },
    /**
     * 
     */
    beforeLoadingFunction() {
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (propData.beforeLoadingFunction && propData.beforeLoadingFunction.length > 0) {
        this.commonCustomEventFunHandle(propData, "beforeLoadingFunction");
      }
    },
    /**
     * 
     */
    afterLoadingFunction() {
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (propData.afterLoadingFunction && propData.afterLoadingFunction.length > 0) {
        this.commonCustomEventFunHandle(propData, "afterLoadingFunction");
      }
    },
    /**
     * 
     * @param {*} callback
     */
    requestPageCommonInterfaceDataBeforeHandle(callback) {
      var interfaceList =
        (this.PageSettingData.page.layout.props.compositeAttr
          ? this.PageSettingData.page.layout.props.compositeAttr.interfaceList
          : []) || [];
      if (interfaceList.length == 0) {
        callback && callback();
        return;
      }
      interfaceList = interfaceList.filter(
        (item) => item.loadTimingType == "pageloadbefore"
      );
      if (interfaceList.length == 0) {
        callback && callback();
        return;
      }
      this.orderlyRequestInterface(interfaceList, 0, this.InterfaceParam(), callback);
    },
    /**
    
     */
    requestPageCommonInterfaceDataBefore(callback) {
      const propData = this.PageSettingData.page.layout.props.compositeAttr || {};
      const that = this;
      this.requestPageCommonInterfaceDataBeforeHandle(() => {
        if (propData?.openFormMode && propData?.formInitDataApi) {
          //
          that.initFormDataToIdmVar(callback);
        } else {
          callback && callback();
        }
      });
    },
    /**
     * IDM
     * @param {*} callback
     */
    initFormDataToIdmVar(callback) {
      const propData = this.PageSettingData.page.layout.props.compositeAttr || {};
      const that = this;
      const requestType = propData?.formInitRequestType || "get";
      const contentType = propData?.formInitContentType || "";
      const extendParamList = propData?.formInitExtendParamList || [];
      const formDataName = IDM.setting.form.initApiParamNameSetting.formData;
      let param = {
        [formDataName]: IDM.url.queryObject()[formDataName],
        [IDM.setting.form.initApiParamNameSetting.formId]: this.pageid,
      };
      extendParamList.forEach((item) => {
        if(item.paramFunction&&item.paramFunction.length){
          let result = IDM.invokeCustomFunctions.apply(this, [
            item.paramFunction,
              {}
            ]);
          param[item.paramKey] = result && result.length > 0?result[0]:"";
          return;
        }
        param[item.paramKey] = IDM.express.replace(item.paramVal,{IDM: IDM, window});
      });
      let headerObject = null;
      if(contentType){
        headerObject = {
          headers:{
            "Content-Type":contentType
          }
        }
      }
      IDM.http[requestType](propData?.formInitDataApi, param,headerObject).then(res=>{
        if(res&&res.data){
          //IDM.form
          IDM.form.setQueryObject(IDM.getExpressData(propData.formInitResultQueryObject,res.data))
          IDM.form.setMetaConfig(IDM.getExpressData(propData.formInitResultMetaConfig,res.data))
          IDM.form.setComponentData(IDM.getExpressData(propData.formInitResultComponentData,res.data))
          IDM.form.setFormData(IDM.getExpressData(propData.formInitResultFormData,res.data))
          IDM.form.setRoleData(IDM.getExpressData(propData.formInitResultRoleData,res.data))
        }else{
          IDM.message.error("")
        }
        callback&&callback();
      }).catch(function (error) {
        IDM.message.error(""+error.message)
        callback&&callback();
      });
    },
    /**
     * 
     * @param {*} checkFirstLoad 
     */
    requestPageCommonInterfaceData(checkFirstLoad) {
      //()
      this.IDM.broadcast.pageLoadCompleteAfter(this.routerId);
      if (checkFirstLoad && this.pageCommonInterfaceLoaded) {
        return;
      }
      this.pageCommonInterfaceLoaded = true;
      const linkList = this.PageSettingData.page.linkList || [];
      if (linkList.length) {
        //
        let propData = this.PageSettingData.page.layout.props.compositeAttr || {};
        if (propData.openJsPlumb === true) {
          //
          this.reconnectionAllModule();
        }
      }
      console.log("");
      this.afterLoadingFunction();

      const interfaceList = (
        this.PageSettingData.page.layout.props.compositeAttr?.interfaceList ?? []
      ).filter((item) => !item.loadTimingType || item.loadTimingType === "pageloadlater");
      if (!interfaceList.length) {
        return;
      }
      this.orderlyRequestInterface(interfaceList, 0, this.InterfaceParam());
    },
    /**
     */
    InterfaceParam() {
      //IDID
      var pageId = this.pageid;
      var moduleIdArray = [],
        componentIdArray = [];
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if (item.packageid != pageId) {
          moduleIdArray.push(item.packageid);
          componentIdArray.push(item.comId);
        }
      });
      //url
      var params = {
        idmPageId: pageId,
        moduleIds: moduleIdArray.join(","),
        componentIds: componentIdArray.join(","),
      };
      let urlObject = IDM.url.queryObject();
      Object.keys(urlObject).forEach((key) => {
        params[key] = urlObject[key];
      });
      return params;
    },
    /**
     * 
     */
    orderlyRequestInterface(interfaceList, index, params, callback, linkMessageObject) {
      if (interfaceList.length <= index) {
        callback && callback();
        return;
      }
      var item = interfaceList[index];
      index = index + 1;
      let that = this;
      //linkMessageObject
      if (linkMessageObject) {
        //
        let _isPass = false;
        try {
          if (item.reloadType == "express") {
            _isPass = IDM.express.replace("@[" + item.reloadExpression + "]", {
              IDM: IDM,
              window,
              linkMessageObject,
            });
          } else if (
            item.reloadType == "customFunction" &&
            item.reloadCustomFunction &&
            item.reloadCustomFunction.length > 0
          ) {
            let itemParam = {
              ...that.commonParam(),
              ...item.reloadCustomFunction[0].param,
              linkMessageObject,
            };
            _isPass =
              window[item.reloadCustomFunction[0].name] &&
              window[item.reloadCustomFunction[0].name].call(this, itemParam);
          }
        } catch (error) {}
        if (!_isPass) {
          that.orderlyRequestInterface(
            interfaceList,
            index,
            params,
            callback,
            linkMessageObject
          );
          return;
        }
      }
      // preAllowFunction
      if (item.preAllowFunction && item.preAllowFunction.length > 0) {
        try {
          let itemParam = {
            ...that.commonParam(),
            ...item.preAllowFunction[0].param,
            linkMessageObject,
          };
          let _isPass =
            window[item.preAllowFunction[0].name] &&
            window[item.preAllowFunction[0].name].call(this, itemParam);
          if (_isPass === false) {
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
            return;
          }
        } catch (error) {}
      }

      //
      if (item.formDataFunction && item.formDataFunction.length > 0) {
        try {
          let itemParam = {
            ...that.commonParam(),
            ...item.formDataFunction[0].param,
            linkMessageObject,
          };
          let formDataParam =
            window[item.formDataFunction[0].name] &&
            window[item.formDataFunction[0].name].call(this, itemParam);
          params = { ...params, ...formDataParam };
        } catch (error) {}
      }

      if (
        item.interfaceType == "datasource" &&
        item.dataSourceSelectData &&
        item.dataSourceSelectData.length
      ) {
        IDM.datasource.request(
          item.dataSourceSelectData[0].id,
          {
            moduleObject: {},
            param: params,
            linkMessageObject,
          },
          function (resData) {
            //
            that.callbackModuleSetContext(item, resData);
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          },
          function (error) {
            //
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          }
        );
      } else if (item.interfaceMode == "post" && item.interfaceUrl) {
        this.$previewApi
          .postPageInterfaceInfo(item.interfaceUrl, params, {
            headers: item.interfaceContentType
              ? {
                  "Content-Type":
                    item.interfaceContentType || "application/json;charset=UTF-8",
                }
              : {},
          })
          .then((res) => {
            //res.data
            //
            that.callbackModuleSetContext(item, res.data || res);
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          })
          .catch(function (error) {
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          });
      } else if (item.interfaceMode == "get" && item.interfaceUrl) {
        this.$previewApi
          .getPageInterfaceInfo(item.interfaceUrl, params)
          .then((res) => {
            //res.data
            //
            that.callbackModuleSetContext(item, res.data || res);
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          })
          .catch(function (error) {
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          });
      } else {
        that.orderlyRequestInterface(
          interfaceList,
          index,
          params,
          callback,
          linkMessageObject
        );
      }
    },
    /**
     * 
     */
    callbackModuleSetContext(item, resData) {
      //data,
      this.pageInterfaceResultObject[item.resultDataName] = resData;
      this.sendBroadcastMessage({
        type: "pageCommonInterface",
        message: resData,
        messageKey: item.resultDataName,
        rangeModule: "",
      });
      if (item.loadTimingType != "pageloadbefore") {
        //
        this.PageSettingData.page.componentsMap.forEach((sitem) => {
          sitem.idmSetContextValue &&
            sitem.idmSetContextValue({
              type: "pageCommonInterface",
              key: item.resultDataName,
              data: resData,
            });
        });
      }
      //
      if (item.interfaceRunLaterFunction && item.interfaceRunLaterFunction.length > 0) {
        var interfaceRunLaterFunction = item.interfaceRunLaterFunction;
        interfaceRunLaterFunction.forEach((sitem) => {
          try {
            window[sitem.name] &&
              window[sitem.name].call(this, {
                ...this.commonParam(),
                customParam: sitem.param,
                resultData: resData,
                _this: this,
              });
          } catch (error) {}
        });
      }
      let propData = this.PageSettingData.page.layout.props.compositeAttr || {};
      //
      if (
        this.mainPage &&
        propData.openIdmTheme &&
        propData.themeFetchType == "pageInterfaceResult" &&
        propData.themeDataName &&
        item.resultDataName == propData.themeDataName
      ) {
        this.themeInterfaceHandle(resData, propData.themeDataName);
      }
    },
    /**
     * 
     */
    pageAttrPropHandle(propDataObject) {
      let propData = propDataObject.compositeAttr;
      this.pagePropData = propData;
      var styleObject = {};
      if (!propData.bgList?.bgList?.length) {
        if (propData.bgSize && propData.bgSize == "custom") {
          styleObject["background-size"] =
            (propData.bgSizeWidth
              ? propData.bgSizeWidth.inputVal + propData.bgSizeWidth.selectVal
              : "auto") +
            " " +
            (propData.bgSizeHeight
              ? propData.bgSizeHeight.inputVal + propData.bgSizeHeight.selectVal
              : "auto");
        } else if (propData.bgSize) {
          styleObject["background-size"] = propData.bgSize;
        }
        if (propData.positionX && propData.positionX.inputVal) {
          styleObject["background-position-x"] =
            propData.positionX.inputVal + propData.positionX.selectVal;
        }
        if (propData.positionY && propData.positionY.inputVal) {
          styleObject["background-position-y"] =
            propData.positionY.inputVal + propData.positionY.selectVal;
        }
      }
      for (const key in propData) {
        if (propData.hasOwnProperty.call(propData, key)) {
          const element = propData[key];
          if (!element && element !== false) {
            continue;
          }
          switch (key) {
            case "defaultIdmTheme":
              if (!this.themeSetObject && this.mainPage) {
                IDM.theme.setCurrentThemeInfo(element);
                this.themeSetObject = element;
              }
              break;
            case "width":
            case "height":
              styleObject[key] = element;
              break;
            case "bgColor":
              if (element.hex8) {
                styleObject["background-color"] =
                  IDM.hex8ToRgbaString(element.hex8) + "  !important";
              }
              break;
            case "box":
              IDM.style.setBoxStyle(styleObject, element);
              break;
            case "bgImgUrl":
              if (!propData.bgList?.bgList?.length) {
                styleObject["background-image"] = `url(${IDM.url.getWebPath(element)})`;
              }
              break;
            case "icoImgUrl":
              try {
                document.head
                  .querySelector("link[rel='icon']")
                  .setAttribute("href", IDM.url.getWebPath(element));
              } catch (error) {}
              //IDM.url.getWebPath(element)
              var link = document.createElement("link");
              link.type = "image/x-icon";
              link.rel = "shortcut icon";
              link.href = IDM.url.getWebPath(element);
              document.getElementsByTagName("head")[0].appendChild(link);
              break;
            case "positionX":
              //

              break;
            case "positionY":
              //

              break;
            case "bgRepeat":
              //
              if (!propData.bgList?.bgList?.length) {
                styleObject["background-repeat"] = element;
              }
              break;
            case "bgAttachment":
              //
              if (!propData.bgList?.bgList?.length) {
                styleObject["background-attachment"] = element;
              }
              break;
            case "border":
              IDM.style.setBorderStyle(styleObject, element);
              break;
            case "font":
              IDM.style.setFontStyle(styleObject, element);
              break;
            case "layout":
              IDM.style.setLayoutStyle(styleObject, element);
              break;
            case "pageMinWidth":
              if (element.inputVal && element.selectVal) {
                styleObject["min-width"] = element.inputVal + element.selectVal;
              }
              break;
            case "bgList":
              if (element && element.style && Object.keys(element.style).length) {
                Object.assign(styleObject, element.style);
              }
              break;
          }
        }
      }
      //
      let filterStyleObject = {};
      IDM.style.setFilterStyle(filterStyleObject, propDataObject.basicAttr, {
        opacity: "pageOpacity",
      });
      IDM.setStyleToPageHead("router_page_" + this.routerId, filterStyleObject);

      this.$nextTick(function (params) {
        if (propData.watermarkType && propData.watermarkType != "none") {
          var _watermarkFont = this.$replace(propData.watermarkFont, {
              IDM: IDM,
            }),
            _watermarkFont1 = this.$replace(propData.watermarkFont1, {
              IDM: IDM,
            });
          //
          IDM.watermark.set(
            this.$refs.idm_page_root,
            propData.watermarkZindex,
            propData.watermarkType != "font" ? propData.watermarkImg : _watermarkFont,
            propData.watermarkType == "image" ? "" : _watermarkFont1,
            {
              fontColor:
                propData.watermarkColor &&
                IDM.hex8ToRgbaString(propData.watermarkColor.hex8),
              fontSize: `${propData.watermarkFontSize}px`,
              width: propData.watermarkSize,
              height: propData.watermarkSize,
              rotate: propData.watermarkRotate,
              imgSize: propData.watermarkImgSize,
              imgLeftSize: propData.watermarkImgLeftSize,
              fontLeftSize: propData.watermarkFontLeftSize,
              opacity: propData.watermarkOpacity / 100,
              topSize: propData.watermarkTopSize,
            }
          );
        } else {
          IDM.watermark.close(this.$refs.idm_page_root);
        }
      });
      IDM.setStyleToPageHead(
        "PAGE_" + this.PageSettingData.id + "_" + this.routerId,
        styleObject
      );
      //jscss
      if (propData.resourceList && propData.resourceList.length > 0) {
        propData.resourceList.forEach((item) => {
          if (item.resourceType == "js") {
            IDM.module.loadJs(
              IDM.url.getWebPath(item.resourceUrl),
              true,
              function () {},
              { async: item.async, defer: item.defer }
            );
          } else {
            IDM.module.loadCss(
              IDM.url.getWebPath(item.resourceUrl),
              true,
              function () {}
            );
          }
        });
      }
      this.convertThemeListAttrToStyleObject(propData);
    },

    /**
     * 
     */
    convertThemeListAttrToStyleObject(propData) {
      var themeList = propData.themeList;
      if (!themeList) {
        return;
      }
      const themeNamePrefix =
        IDM.setting &&
        IDM.setting.applications &&
        IDM.setting.applications.themeNamePrefix
          ? IDM.setting.applications.themeNamePrefix
          : "idm-theme-";
      for (var i = 0; i < themeList.length; i++) {
        var item = themeList[i];
        //item.keykey
        //item.mainColor
        //item.minorColor
        if (item.key != IDM.theme.getCurrentThemeInfo()) {
          //
          continue;
        }
        let cssObject_color_main = {
          color: item.mainColor ? item.mainColor.hex8 + " !important" : "",
        };
        let cssObject_border_main = {
          "border-color": item.mainColor ? item.mainColor.hex8 : "",
        };
        let cssObject_background_main = {
          "background-color": item.mainColor ? item.mainColor.hex8 : "",
        };
        let cssObject_color_minor = {
          color: item.minorColor ? item.minorColor.hex8 : "",
        };
        let cssObject_border_minor = {
          "border-color": item.minorColor ? item.minorColor.hex8 : "",
        };
        let cssObject_background_minor = {
          "background-color": item.minorColor ? item.minorColor.hex8 : "",
        };
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm_module_user_defined`,
          {
            ...cssObject_border_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm_module_user_defined>div`,
          {
            ...cssObject_background_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .user-defined-logo-svg,
          .${
            themeNamePrefix + item.key
          } .idm_page_user_defined_leftbar svg.user-defined-add-svg,
          .${
            themeNamePrefix + item.key
          } .idm_page_user_defined_modal_moduleitem .user-defined-modal-changeseats,
          .${
            themeNamePrefix + item.key
