      );
      if (!opModule) {
        return false;
      }
      if (opModule.renderType == "firstrender" || opModule.renderType == "activerender") {
        opModule.renderLicence = true;
        if (opModule.renderType == "firstrender" && opModule.loaded) {
          return _.cloneDeep(opModule);
        } else {
          //false
          this.IDM.broadcast.pageModuleLoadCompleteBefore(this.routerId);
          //
