    initModulePropCustomBuildCodeRecursive(attrObject, functionList) {
      if (IDM.type(attrObject) === "array") {
        //customBuildCodecustomSourceCodenameIdmAutoGenFun_idmBuiltin=false
        for (let index = 0; index < attrObject.length; index++) {
          const item = attrObject[index];
          if (
            item.customBuildCode &&
            item.customSourceCode &&
            item.name &&
            item.idmBuiltin === false &&
            item.name.startsWiths("IdmAutoGenFun_") &&
            IDM.type(item.customBuildCode) === "string" &&
            IDM.type(item.customSourceCode) === "string"
          ) {
            functionList.push(_.cloneDeep(item));
          } else {
            this.initModulePropCustomBuildCodeRecursive(item, functionList);
          }
        }
      } else if (IDM.type(attrObject) === "object") {
        Object.keys(attrObject).forEach((key) => {
          this.initModulePropCustomBuildCodeRecursive(attrObject[key], functionList);
        });
      }
