      // id:ID
      // idm-com-idID
      // idm-com-classid
      // idm-com-classname
      // idm-container-index
    },
    moduleMountCompleteCommon(module, opModule) {
      let that = this;
      var moduleDom = $("#router_page_" + this.routerId + " #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(">*[idm-ctrl]");
      var idmCtrl = moduleInnerDom.attr("idm-ctrl"),
        idmCtrlId = moduleInnerDom.attr("idm-ctrl-id"),
        domId = moduleInnerDom.attr("id");
      if (!domId) {
        moduleInnerDom.attr("id", idmCtrlId);
      }
      switch (idmCtrl) {
        case "drag_container": //
          moduleDom.addClass("drag_container");
          break;
        case "drag_container_inlineblock": //
          moduleDom.addClass("drag_container drag_container_inlineblock");
          break;
        case "idm_module": //
          break;
      }

      //
      if (!this.pageModuleDataIsLoaded(null, true)) {
        this.$nextTick(function () {
          this.initModuleTreeToPage(null, true, this.PageSettingData.page.layout);
        });
      } else if (this.pageModuleDataIsLoaded(null, true, "loaded")) {
        this.requestPageCommonInterfaceData(true);
      }
      // this.dynamicWaitExpendObject
      if (opModule.dynamicModule || opModule.dynamicModuleMessage) {
        // dynamicWaitExpendObject 
        let globalIdArray = opModule.globalId.split(".");
        const parentModuleId =
          globalIdArray[globalIdArray.length - 2] || globalIdArray[0];
        this.dynamicWaitExpendObject.forEach((ditem, index) => {
          if (
            globalIdArray.includes(ditem.packageid) &&
            ditem.allModulePackageId.includes(opModule.packageid)
          ) {
            let parentModuleTree = this.getModuleTree(
              this.PageSettingData.page.layout.children,
              ditem.packageid
            );
            //ditem.allModulePackageId
            let allModulePackageId = _.cloneDeep(ditem.allModulePackageId);
            this.pageModuleDataIsLoaded(
              parentModuleTree.children,
              true,
              "loaded",
              allModulePackageId
            );
            if (allModulePackageId.length == 0) {
              // linkageResult
              IDM.broadcast.send({
                type: "linkageResult",
                message: ditem.itemData,
                rangeModule: ditem.allModulePackageId,
                messageKey: ditem.sendMessageKey || "dynamicRenderModule",
              });
              this.dynamicWaitExpendObject.splice(index, 1);
            }
          }
        });
      }
    },
    /**
