var formObject = {
    //
    _Q_:{},
    //
    __FORMDATA:null,
    //
    METACONFIG:[],
    //
    COMPONENTDATA:[],
    //&
    ROLEDATA:null,
    //
    FLOWNODE:null,
}
function getQueryObject(){
    return this.formObject._Q_;
}
function setQueryObject(_Q_){
    return this.formObject._Q_=_Q_;
}
function getFormData(){
    return this.formObject.__FORMDATA;
}
function setFormData(Data){
    return this.formObject.__FORMDATA=Data;
}
function getMetaConfig(){
    return this.formObject.METACONFIG;
}
function setMetaConfig(Data){
    return this.formObject.METACONFIG=Data;
}
function getComponentData(){
    return this.formObject.COMPONENTDATA;
}
function setComponentData(Data){
    return this.formObject.COMPONENTDATA=Data;
}
function getRoleData(){
    return this.formObject.ROLEDATA;
}
function setRoleData(Data){
    return this.formObject.ROLEDATA=Data;
}
function getFlowNode(){
    return this.formObject.FLOWNODE;
}
function setFlowNode(Data){
    return this.formObject.FLOWNODE=Data;
}

export default {
    formObject,
    getQueryObject,
    setQueryObject,
    getFormData,
    setFormData,
    getMetaConfig,
    setMetaConfig,
    getComponentData,
    setComponentData,
    getRoleData,
    setRoleData,
    getFlowNode,
    setFlowNode
}