import 'core-js/stable'
import 'regenerator-runtime/runtime'

import Vue from 'vue'
import App from './App.vue'
import store from '@/store'
import router from './router'

//jquery
import $ from 'jquery'
window.jQuery = $;
window.$ = $;

//
import './utils'
//IDM
import './utils/idm_mix'

IDM.layer.run();
//layer
window.layer = IDM.layer;

//VueRouterIDM
IDM.router.setVueRouter(router);

/**
 * antd
 * 100KBjs32KBcssplugins/antd.jspreview.vuea-drawer
 */ 
import {
  Drawer,
  Button,
  Modal
} from 'idm-antv-ui';
import IDM from './utils'
Vue.use(Drawer);
Vue.use(Button);
Vue.use(Modal);

// import VConsole from 'vconsole';
// const vConsole = new VConsole();

Vue.config.devtools = true
Vue.prototype.IDM = IDM;
Vue.prototype.$http = IDM.http;
Vue.config.productionTip = false
new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
